import React from 'react';
import s from './Post.module.css';

type MessageType = {
  message: string
  likesCount: number
}

const Post: React.FC<MessageType> = (props:MessageType) => {

  return (
        <div className={s.item}>
          <img src='data:image/jpeg;base64,' />
          {props.message}
          
          <div>
            <span>like</span>{props.likesCount}
          </div>
        </div>
        
  )

}

export default Post;